@import "../variables.scss";

.container {
  margin-top: 20px;
  display: flex;
  width: 100%;
}

.contact {
  display: flex;
  flex-direction: row;
  width: 100%;
  background-color: $transparentBgColor;
  padding: 0 70px;
  justify-content: space-between;
  flex-wrap: wrap;
}

.row {
  height: 30px;
  display: flex;
  flex-direction: row;
  align-items: center;
  padding: 10px 0;
  justify-content: flex-start;

  color: $textColor;
  fill: $textColor;
}

.row:hover {
  color: $bandColor;
  fill: $bandColor;
}

.icon {
  height: 100%;
  width: 40px;
  margin-right: 10px;
}

a:visited {
  color: inherit;
}

.text {
  a {
    color: $textColor !important;
  }
}
