@import "../variables.scss";

.pageContainer {
  height: 100%;
  width: 100%;
  background-image: url("../../assets/images/background-with-logo.jpg");
  background-attachment: fixed;
  background-position: center;
  background-size: cover;
  font-family: "mic-32";
  color: $textColor;
}

.scrollableContent {
  overflow-y: auto;
  height: 100%;
  display: flex;
  flex-direction: column;
}

.content {
  flex: 1;
}
